<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :gridName="gridName"
      :headers="gridHeaders"
      :getDataCb="getDataCb"
      :toolbarTitle="toolbarTitle"
      :isHeaderConfigurable="true"
    >
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <rf-point-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :pluginName="pluginName"
          :immediateParent="immediateParent"
        ></rf-point-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <rf-point-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :pluginName="pluginName"
          :immediateParent="immediateParent"
        ></rf-point-form>
      </template>
      <template v-slot:afterActions="{ item, refreshGrid }">
        <tooltip-icon-button
          btnTooltip="Assign Point Action"
          btnIcon="fa-yelp"
          btnColor="orange"
          v-if="item.item.thing_class === 'point'"
          :onClick="() => toggleDialog(dialogNames.pointWriterActionDialog, item.item, refreshGrid)"
        ></tooltip-icon-button>
      </template>
    </generic-grid>
    <v-dialog v-model="dialog[dialogNames.pointWriterActionDialog]" persistent max-width="500px">
      <point-writer-actions-form
        v-if="dialog[dialogNames.pointWriterActionDialog]"
        :onCancel="() => closeDialog(dialogNames.pointWriterActionDialog)"
        :item="selectedItem"
        :priorityArrayOnly="true"
        :onSave="onAssignPointWriteAction"
      ></point-writer-actions-form>
    </v-dialog>
  </div>
</template>
<script>
import RfPointForm from './rfPointForm.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import { rfPointService } from '../../../services/rfPointService';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import PointWriterActionsForm from '../WriterActions/pointWriterActionsForm.vue';

const dialogNames = {
  pointWriterActionDialog: 'pointWriterActionDialog',
};

export default {
  name: 'rf-point-grid',
  components: { GenericGrid, RfPointForm, TooltipIconButton, PointWriterActionsForm },
  props: ['params', 'getDataCb', 'immediateParent', 'pluginName'],
  data() {
    return {
      dialog: {
        [dialogNames.pointWriterActionDialog]: false,
      },
      dialogNames: dialogNames,
      selectedItem: {},
      syncGrid: f => f,
      gridName: gridNames.RF_POINTS_GRID,
      service: rfPointService,
      toolbarTitle: 'Point',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    toggleDialog(dialogName, item, syncGrid) {
      this.selectedItem = item;
      this.syncGrid = syncGrid;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    onAssignPointWriteAction(formvalues) {
      const pointUUID = this.selectedItem.uuid;
      return rfPointService.assignPointArray(pointUUID, formvalues);
    },
  },
};
</script>
