<template>
  <dynamic-form
    :key="rerender"
    v-if="parentModulePath"
    :parent-module-path="parentModulePath"
    :item="item"
    :title="title"
    :isEdit="isEdit"
    :onSave="onSave"
    :onCancel="onCancel"
    moduleName="point"
    :initialFormValues="formValues"
  ></dynamic-form>
</template>
<script>
import DynamicForm from '../../FormComponents/dynamicForm.vue';
export default {
  name: 'rf-point-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'immediateParent', 'pluginName'],
  data: () => ({
    rerender: 0,
    formValues: {},
    parentModulePath: null,
  }),
  components: { DynamicForm },
  computed: {
    lowerCaseTitle() {
      return 'point';
    },
    titleCase() {
      return 'Point';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.item,
      };
      this.rerender++;
    }

    if (this.immediateParent) {
      this.parentModulePath = this.pluginName;
      this.formValues = {
        ...this.formValues,
        plugin_name: this.pluginName,
        device_uuid: this.immediateParent.uuid,
      };
      this.rerender++;
    }
  },
};
</script>
