<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> fa-angle-left </v-icon>
      </v-btn>
      <span>Points</span><span class="caption pl-3" v-if="deviceName">Device: {{ deviceName }} </span>
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <rf-point-grid
            v-if="params.uuid"
            :pluginName="pluginName"
            :key="rerender"
            :params="params"
            :getDataCb="getDataCb"
            :immediateParent="immediateParent"
          >
          </rf-point-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import RfPointGrid from './rfPointGrid.vue';
import * as uiService from '../Common/uiService';
import IconButton from '../../Common/IconButton.vue';
import routeConstant from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfNetworkService } from '../../../services/rfNetworkService';

const baseHref = routeConstant.rfNetworkDevicePoint;
const initialBreadCrumbItem = {
  text: 'Networks',
  disabled: false,
  link: baseHref,
};

export default {
  name: 'rf-points',
  components: {
    IconButton,
    RfPointGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        with_points: true,
        with_priority: true,
      },
      rerender: 0,
      pluginName: null,
      renderCount: 0,
      immediateParent: {},
      networkuuid: null,
      deviceName: '',
      gridData: {},
      network: {},
      breadcrumbItems: [initialBreadCrumbItem],
    };
  },
  watch: {
    '$route.params.deviceId': function (val, oldVal) {
      if (val != oldVal) {
        this.params = { ...this.params, uuid: this.$route.params.deviceId };
        this.updateBreadCrumbs();
        this.renderCount++;
      }
    },
    'networkNamesMap': function () {
      this.updateBreadCrumbs();
    },
  },
  computed: {
    networkNamesMap() {
      return this.$store.state.networkNamesMap;
    },
  },
  mounted() {
    this.fetchNetwork();
    this.updateBreadCrumbs();
    this.params = { ...this.params, uuid: this.$route.params.deviceId };
  },
  methods: {
    fetchNetwork() {
      rfNetworkService.fetchById(this.$route.params.networkId).then(network => {
        this.network = network;
        this.pluginName = this.network.plugin_name;
        this.rerender++;
      });
    },
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.immediateParent = _get(this.gridData, 'extraData', this.immediateParent);
    },
    updateBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push(initialBreadCrumbItem);
      this.breadcrumbItems.push({
        text: uiService.getNameById(this.networkNamesMap, this.$route.params.networkId),
        align: 'center',
        disabled: false,
        link: routeConstant.rfDevices.replace(':networkId', this.$route.params.networkId),
      });
      this.breadcrumbItems.push({
        text: uiService.getNameById(this.networkNamesMap, this.$route.params.deviceId),
        align: 'center',
        disabled: false,
        link: routeConstant.rfPoints
          .replace(':networkId', this.$route.params.networkId)
          .replace(':deviceId', this.$route.params.deviceId),
      });
    },
  },
};
</script>
